import React, {useState, useEffect} from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import styled from "styled-components";
import { QUESTIONS, Quiz } from './questionData';

const Result = () =>{
    const navigate = useNavigate();
    const location = useLocation();
    const [result, setResult] = useState("");
    const [isFinal, setIsFinal] = useState(false);

    useEffect(()=>{
        showResult();
    });
    
    const TransTop = () => {
        console.log(`Quiz index : ${Quiz.index}`);
        console.log(`Quiz MIN : ${Quiz.MIN}`);
        console.log(`Question length : ${QUESTIONS.length}`);
        Quiz.correctCount = Number(location.state.correct);
        if(Quiz.MIN === QUESTIONS.length){
            Quiz.MIN = 0;
            navigate("/final");
        } else {
            navigate("/");
        }
       
        
    }

    const showResult = () => {
        if (Quiz.MIN === QUESTIONS.length) {
            setIsFinal(true);
        }
        if(Number(location.state.correct) === QUESTIONS.length) {
            setResult("");
        } else {
            setResult(`${location.state.correct}!!`);
        }
    }

    const showFinalView = (isFinal) => {
        if (isFinal) {
            return <Button onClick={TransTop}></Button>
        } else {
            return <Button onClick={TransTop}></Button>
        }
    }
    
    return(
        <React.StrictMode>
            <TITLE></TITLE>
            <RESULT>{result}</RESULT>
            <RESULT>{location.state.point}<ul /></RESULT>
            {showFinalView(isFinal)}
        </React.StrictMode>
        
    );
}

const TITLE = styled.h1`
    font-family: " ProN";
    margin-top: 32px;
    margin-bottom: 64px;
    font-size: 56px;
`;

const RESULT = styled.h2`
    font-family: " ProN";
    line-height: 64px;
    margin-top: 16px;
    magin-bottom: 64px;
    font-size: 40px;
`;

const Button = styled.button`
    width: 100%;
    height: 48px;
    margin-top: 150px;
    line-height: 48px;
    border-radius: 8px;
    color: white;
    background-color: #4366ff;
    font-family: " ProN";
    
`;

export {Result};